import React from 'react';
import { TBIconMenu } from '@ali/we-design-next';
import { HoverToolbarHooks } from '@ali/4ever-component';
import { StyleButtonIcon } from '../styled';
import { ImageStyleOverlay, IImageStyleOverlayProps } from '../imageStyleOverlay';

export interface IImageStyleIconMenu extends Omit<IImageStyleOverlayProps, 'onClick'> {
  onOverlayVisibleChange: (visible: boolean) => void;
  testid?: string;
}

export const ImageStyleIconMenu: React.FC<IImageStyleIconMenu> = (props) => {
  const { controller, getImageNode, locale, onOverlayVisibleChange, testid } = props;
  const getContainer = HoverToolbarHooks.useGetContainer();
  const [isOverlayVisible, setOverlayVisible] = HoverToolbarHooks.usePopoverVisible('image.style', onOverlayVisibleChange);
 
  return (
    <TBIconMenu
      icon={StyleButtonIcon}
      overlay={
        <ImageStyleOverlay
          getImageNode={getImageNode}
          controller={controller}
          locale={locale}
          onClick={() => { setOverlayVisible(false)}}
        />
      }
      overlayVisible={isOverlayVisible}
      onVisibleChange={setOverlayVisible}
      getContainer={getContainer}
      title={locale.recommendStyle}
      tooltip={locale.recommendStyle}
      testid={testid}
    />
  );
};
